* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.component {
    
    .component__backdrop {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        z-index: 20;
        background-color: rgba(0, 0, 0, 0.75);
    }

    .component__sity-list {
        position: fixed;
        top: 5%;
        left: 5%;
        right: 5%;
        width: 90%;
        background-color: white;
        padding: 1rem;
        border-radius: 14px;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
        z-index: 30;
        // animation: slide-down 300ms ease-out forwards;
    }
}
